import React, {useState, useEffect} from "react";
import { Container, Row, Col, Table } from "react-bootstrap";
import prevArrow from "../../../assets/images/prev.svg";
import nextArrow from "../../../assets/images/next.svg";
import { useNavigate, Link } from 'react-router-dom';
import Breadcrumb from "../Breadcrumb";
import Swal from "sweetalert2";

import Loader from "../../../Components/Loader";
import axios from "axios"; 
import API_ENDPOINTS from "../../../utilities/apiEndPoints";
import { useAuth } from "../../../context/AuthContext";
import formatDate from '../../../Components/FormatDate'
import toDDMMYYYY from "../../../Components/NewFormatDate";



export default function SubmissionHistory() {
  const navigate = useNavigate();
  const { userData, token } = useAuth();
  const [isLoading, setIsLoading] = useState(false);
  const [submissionHistory, setSubmissionHistory] = useState([])
  const [showDocPopup, setShowDocPopup] = useState(false);
  const [viewDocumentHistory, setViewDocumentHistory] = useState({})

  useEffect(()=>{
    const fetchHistoryDocuments = async () => {
      setIsLoading(true);
      try {
        const response = await axios({
          method: 'get',
          url: `${process.env.REACT_APP_API_BASE_URL}${API_ENDPOINTS.DOCUMENT_SUBMISSION_HISTORY}`,
          headers: { 
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
        });

        if (response.data.success) {
          const { data } = response.data;
         // console.log(data)
           setSubmissionHistory(data);
         
        } else {
          throw new Error(response.data.message || 'Failed to fetch  details');
        }
      } catch (error) {
        console.error("Error fetching  details:", error);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: error.response?.data?.message || error.message || 'Failed to fetch  details',
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchHistoryDocuments();
   },[userData.id])


   const viewHistory=async (id, type)=>{
    //console.log(id, type)
    setIsLoading(true);
    try {
      const response = await axios({
        method: 'get',
        url: `${process.env.REACT_APP_API_BASE_URL}${API_ENDPOINTS.VIEW_HISTORY_DOCUMENT}/${id}`,
        headers: { 
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
      });

      if (response.data.success) {
        const { data } = response.data;
       // console.log(data)
       setViewDocumentHistory(data);
       setShowDocPopup(true);
       
      } else {
        throw new Error(response.data.message || 'Failed to fetch  details');
      }
    } catch (error) {
      console.error("Error fetching  details:", error);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: error.response?.data?.message || error.message || 'Failed to fetch  details',
      });
    } finally {
      setIsLoading(false);
    }
   }
 
  return (
    <div className="mainContent">
      <Breadcrumb />
      <div className="mainContent__assets">
      {isLoading && <Loader />}
        <Container>
          <Row>
            <Col lg={12}>
              <div className="personalDetailsForm">
                <div className="secIntro ">
                  <div className="secIntro__left">
                    <Link onClick={() => navigate(-1)}>&lt; Go back </Link>
                    <h2>My Banking Details</h2>
                  </div>
                  <div className="secIntro__btn">
                    <Link
                      to="/instructor/account-setting/my-documents"
                      className="btn btn-teal"
                    >
                      Back to Document
                    </Link>
                  </div>
                </div>
                {/* Upcoming Booking */}
                <div className="upcomingBooking">
                  <div className="accordians">
                    <div className="accordians__card">
                      <div className="accordians__description">
                        <Table>
                          <thead>
                            <tr>
                              <th>Submission Date</th>
                              <th>Status</th>
                              <th>Document</th>
                              <th></th>
                            </tr>
                          </thead>
                          <tbody>
                            {submissionHistory.length > 0 ? submissionHistory.map((history, index) => (
                              <tr key={index}>
                                <td>{history.submission_date}</td>
                                <td>{history.document_status}</td>
                                <td>{history.document_type}</td>
                                <td><button to={history.id} onClick={()=>{viewHistory(history.id, history.document_type)}} className="btn btn-grey">VIEW</button></td>
                              </tr>
                            )) :'No History Found'}
                          </tbody>
                        </Table>
                        {/* Pagination */}
                        <div className="pagination-container">
                          <p>Displaying intructor submissions {submissionHistory.length} of {submissionHistory.length} in total</p>
                          {/* <button className="btn ">
                            <img src={prevArrow} alt="" />
                          </button>
                          <span>1</span>
                          <Link>2</Link>
                          <Link>3</Link>
                          <button className="btn ">
                            <img src={nextArrow} alt="" />
                          </button> */}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>

        {showDocPopup && (
                            <div className="popup">
                              <Container>
                                <Row>
                                  <Col lg={12}>
                                    <div className="popup__wrp personalDetailsForm__wrp">
                                      <div className="personalDetailsForm__top text-center position-relative">
                                        <h4>Document History "{viewDocumentHistory?.document_type}"</h4>
                                        <button type="button" className="popup__close" onClick={() => setShowDocPopup(false)}> X</button>
                                      </div>

                                      <div className="personalDetailsForm__bottom">
                                      <div className="RegistrationForm__field" style={{textAlign:'left'}}>
                                        
                                      </div>
                                        <div className="RegistrationForm__field">

{viewDocumentHistory?.document_type === 'instructors_licence' && ( <>
                                        <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make">Document status</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.document_status}/>
                                          </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> Instructor Licence Expiry</label>
                                            <input type="text"className="form-control" value={toDDMMYYYY(viewDocumentHistory?.instructor_licence_expiry)}/>
                                          </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> Document submission date</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.submission_date}/>
                                          </div>
                                        </div>
                                         </div>

                                         <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                        <div className="ProfileForm__group">
                                        <label htmlFor="make"> Instructor Licence Validity Image</label>
                                        <img src={viewDocumentHistory?.instructor_licence_validity_image} style={{width:'100px',height:'auto',border:'1px solid #e2e2e2'}}/>
                                        </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                        <div className="ProfileForm__group">
                                        <label htmlFor="make"> Instructor Licence Signature Image</label>
                                        <img src={viewDocumentHistory?.instructor_licence_signature_image} style={{width:'100px',height:'auto',border:'1px solid #e2e2e2'}}/>
                                        </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                        <div className="ProfileForm__group">
                                        <label htmlFor="make"> Instructor Licence Detail Image</label>
                                        <img src={viewDocumentHistory?.instructor_licence_holder_details_image} style={{width:'100px',height:'auto',border:'1px solid #e2e2e2'}}/>
                                        </div>
                                        </div>
                                        </div>
 </>) }

 {viewDocumentHistory?.document_type === 'wwcc' && ( <>
                                        <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make">Document status</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.document_status}/>
                                          </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> WWCC Full name</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.wwcc_full_name}/>
                                          </div>
                                        </div>
                                        </div>
                                        <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> WWCC Date of Birth</label>
                                            <input type="text"className="form-control" value={toDDMMYYYY(viewDocumentHistory?.wwcc_date_of_birth )}/>
                                          </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> WWCC Expiry</label>
                                            <input type="text"className="form-control" value={toDDMMYYYY(viewDocumentHistory?.wwcc_expiry)}/>
                                          </div>
                                        </div>
                                        </div>
                                        <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> WWCC Verification date</label>
                                            <input type="text"className="form-control" value={toDDMMYYYY(viewDocumentHistory?.wwcc_verification_date)}/>
                                          </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> WWCC Number</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.wwcc_number}/>
                                          </div>
                                        </div>
                                        </div>
                                        <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> Document submission date</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.submission_date}/>
                                          </div>
                                        </div>
                                         </div>

                                         <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                        <div className="ProfileForm__group">
                                        <label htmlFor="make" style={{'display':'block'}}> Instructor Licence Validity Image &nbsp;</label>
                                        <img src={viewDocumentHistory?.wwcc_document} style={{width:'100px',height:'auto', border:'1px solid #e2e2e2'}}/>
                                        </div>
                                        </div>
                                        
                                       
                                        </div>
 </>) }

 {viewDocumentHistory?.document_type === 'drivers_licence' && ( <>
                                        <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make">Document status</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.document_status}/>
                                          </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> Driver Licence Expiry</label>
                                            <input type="text"className="form-control" value={toDDMMYYYY(viewDocumentHistory?.drivers_licence_expiry)}/>
                                          </div>
                                        </div>
                                        </div>
                                        <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                          <div className="ProfileForm__group">
                                            <label htmlFor="make"> Document submission date</label>
                                            <input type="text"className="form-control" value={viewDocumentHistory?.submission_date}/>
                                          </div>
                                        </div>
                                         </div>

                                         <div className="RegistrationForm__group">
                                        <div className="RegistrationForm__field">
                                        <div className="ProfileForm__group">
                                        <label htmlFor="make" style={{'display':'block'}}> Driver Licence Front Image</label>
                                        <img src={viewDocumentHistory?.front_image} style={{width:'100px',height:'auto', border:'1px solid #e2e2e2'}}/>
                                        </div>
                                        </div>
                                        <div className="RegistrationForm__field">
                                        <div className="ProfileForm__group">
                                        <label htmlFor="make" style={{'display':'block'}}> Driver Licence Back Image</label>
                                        <img src={viewDocumentHistory?.back_image} style={{width:'100px',height:'auto', border:'1px solid #e2e2e2'}}/>
                                        </div>
                                        </div>
                                       
                                        </div>
 </>) }


                                      </div>
                                        
                                    </div>
                                    </div>
                                  </Col>
                                </Row>
                              </Container>
                            </div>
                          )}

      </div>
    </div>
  );
}
